import xinput from '../../wxcomponents/common/xinput.vue'

export default {
	data() {
		return {
			accessToken:'',
			fkey:'',
			skey:'',
			xtype: 'text'
		}
	},
	methods:{
		bindKey(e){
			this.fkey = e.inputValue
		},
		bindSkey(e){
			this.skey = e.inputValue
		},
		onReg(e){
			let reg =/^(?!([a-zA-Z]+|\d+)$)[a-zA-Z\d]{6}$/
			let str = e
			if(str.match(reg) == null){
				uni.showToast({
					title: '密码格式不正确',
					icon:'none'
				})
			} else{
				uni.reLaunch({
					url: '../index/index'
				})
			}
		},
		bindSubmit(){
			let skey = this.skey
			let fkey = this.fkey
			if( skey == fkey){
				this.onReg(fkey)
				this.onReg(skey)
			} else{
				uni.showToast({
					title: '两次密码输入不一致',
					icon:'none'
				})
			}
		}
	},
	components: {
		xinput
	},
}

